
.slideshow {
  max-width: 80%;
  padding: 0 2em;
}
.slideshow .quote {
    color: white;
    text-shadow: 1px 2px 3px black;
    font-family: Copperplate, "Copperplate Gothic Light", sans-serif;
}
.slideshow .quote-text {
      font-size: 4vw;
}
.slideshow .quote-author {
      font-size: 3vw;
      text-align: right;
}
.navbar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 1em;
}
.navbar button {
    color: white;
    text-shadow: 1px 2px 3px black;
    background-color: rgba(150, 150, 150, 0.1);
    border-color: white;
}



